<template>
  <div class="message-page">
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.message_base|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="js">
              {{ code.message_base }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-default" @click="oninfoType"></button>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
        <x-panel :code="code.message_action|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="js">
              {{ code.message_action }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-default" @click="onAction"></button>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.message_type|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="js">
              {{ code.message_type }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-default" @click="onSuccessType"></button><button class="cd-btn cd-btn-ghost-default" @click="onWarningType"></button><button class="cd-btn cd-btn-ghost-default" @click="onDangerType"></button>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
        <x-panel :code="code.message_delay|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="js">
              {{ code.message_delay }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-default" @click="onDelay">10</button>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.message_api" 
      :attrData="attrData" 
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './message';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'type',
    dec: '',
    type: 'String',
    value: '',
    optional: '<code>danger</code> <code>success</code> <code>info</code> <code>warning</code>'
  },
  {
    name: 'message',
    dec: '',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'action',
    dec: `<code>title</code> <code>url</code> <code>template</code>
      <code>template</code>html<code>title</code> <code>url</code>`,
    type: 'Object',
    value: '',
    optional: '--'
  },
  {
    name: 'action.title',
    dec: ``,
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'action.url',
    dec: ``,
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'action.template',
    dec: ``,
    type: 'String|Html',
    value: '',
    optional: '--'
  },
  {
    name: 'delay',
    dec: '3',
    type: 'Number',
    value: '3000',
    optional: '--'
  },
  {
    name: 'offset',
    dec: '',
    type: 'Number',
    value: '80',
    optional: '--'
  },
  {
    name: 'zIndex',
    dec: 'z',
    type: 'Number',
    value: '9999',
    optional: '--'
  },
  { 
    name: 'animate',
    dec: '',
    type: 'Object',
    value: '',
    optional: '--'
  },
  { 
    name: 'animate.enter',
    dec: '',
    type: 'String',
    value: '<code>cd-animated cd-fadeInDownSmall</code>',
    optional: '--'
  },
  { 
    name: 'animate.exit',
    dec: '',
    type: 'String',
    value: '<code>cd-animated cd-fadeOutUp</code>',
    optional: '--'
  }
];

const hookData = [
  {
    name: 'close',
    dec: '',
    callback: '--',
    args: ''
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  methods: {
    oninfoType() {
      cd.message({
        type: 'info',
        message: ''
      });
    },
    onDangerType() {
      cd.message({
        type: 'danger',
        message: '',
      })
    },
    onSuccessType() {
      cd.message({
        type: 'success',
        message: '',
      })
    },
    onWarningType() {
      cd.message({
        type: 'warning',
        message: '',
      })
    },
    onAction() {
      cd.message({
        type: 'info',
        message: '',
        action: {
          title: '',
          href: '#'
        }
      });
    },
    onDelay() {
      cd.message({
        type: 'info',
        message: '10',
        delay: '10000'
      });
    }
  }
}
</script>

<style lang="less">
.message-page .cd-btn {
  margin-right: 16px;
}
</style>



