<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.radio_base|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.radio_base }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.radio_base"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.radio_size|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.radio_size }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.radio_size"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.radio_disabled|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.radio_disabled }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.radio_disabled"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.radio_api" 
      :dataApiData="dataApiData" 
      :attrData="attrData" 
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './radio';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const dataApiData = [
  {
    name: 'data-toggle',
    dec: 'JS',
    type: 'String',
    value: 'cd-radio'
  }
];

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'change',
    dec: '',
    callback: '--',
    args: 'args1: event<br>args2: value'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      dataApiData,
      attrData,
      hookData
    }
  },
  methods: {
  }
}
</script>

<style lang="less">

</style>



