export const message_base = `
cd.message({
  type: 'info',
  message: ''
});
`;

export const message_type = `
cd.message({
  type: 'success',
  message: ''
});

cd.message({
  type: 'warning',
  message: ''
});

cd.message({
  type: 'danger',
  message: ''
});
`;

export const message_action = `
cd.message({
  type: 'info',
  message: '',
  action: {
    title: '',
    href: '#'
  }
});
`;

export const message_delay = `
cd.message({
  type: 'info',
  message: '',
  delay: '10000'
});
`;

export const message_api = `
cd.message({
  type: 'danger',
  message: '',
  delay: '3000',
  z_index: '9999',
  offset: '80',
  action: {
    title: '',
    url: '',
    template: ''
  },
  animate: {
    enter: 'cd-animated cd-fadeInDownSmall',
    exit: 'cd-animated cd-fadeOutUp'
  },
})
`;



