<template>
  <div class="switch-page">
    <div class="site-content-title"></div>
    <div class="site-content-des">
       Radio 
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.switch_base|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.switch_base }}
            </code></pre>
          </div>
          <div class="cd-row cd-mb16" slot="style">
            <div class="col-xs-8" v-html="code.switch_base"></div>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.switch_disabled|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.switch_disabled }}
            </code></pre>
          </div>
          <div class="cd-row cd-mb16" slot="style">
            <div class="col-xs-8" v-html="code.switch_disabled"></div>
          </div>
          <div class="" slot="dec">
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.switch_api" 
      :dataApiData="dataApiData" 
      :attrData="attrData" 
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './switch';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const dataApiData = [
  {
    name: 'data-toggle',
    dec: 'JS',
    type: 'String',
    value: 'cd-switch'
  }
];

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'change',
    dec: '',
    callback: '--',
    args: 'args1: value'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      dataApiData,
      attrData,
      hookData
    }
  },
  mounted() {
    
  },
  methods: {
  }
}
</script>

<style lang="less">
.switch-page .cd-switch {
  margin-right: 16px;
}
</style>



