export const tag_type = `
<span class="cd-tag"></span>

<span class="cd-tag" id="cd-tag">
  
  <i class="cd-icon cd-icon-danger"></i>
</span>
`;

export const tag_color = `
<span class="cd-tag cd-tag-default"></span>
<span class="cd-tag cd-tag-green"></span>
<span class="cd-tag cd-tag-red"></span>
<span class="cd-tag cd-tag-orange"></span>
<span class="cd-tag cd-tag-blue"></span>
<span class="cd-tag cd-tag-purple"></span>
<span class="cd-tag cd-tag-pink"></span>
<span class="cd-tag cd-tag-yellow"></span>
`;

export const tag_api = `
cd.tag({
  el: '#cd-tag',
}).on('close', ($tag) => {
  console.log('tag', $tag);
})
`;


