<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.modal_base|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.modal_base }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-default" type="button" @click="modal"></button>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.modal_api" 
      :attrData="attrData"
      :eventData="eventData"
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './modal';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'ajax',
    dec: 'ajax',
    type: 'Boolean',
    value: 'false',
    optional: '--'
  },
  {
    name: 'url',
    dec: 'ajax',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'maskClosable',
    dec: '',
    type: 'Boolean',
    value: 'true',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'ok',
    dec: '',
    callback: '--',
    args: 'args1: $modaljqueryargs2: this'
  },
  {
    name: 'cancel',
    dec: '',
    callback: '--',
    args: 'args1: $modaljqueryargs2: this'
  },
];

const eventData = [
  {
    name: 'close',
    dec: '',
    callback: '',
    args: '',
  }
]

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData,
      eventData
    }
  },
  methods: {
    modal() {
      cd.modal({
        el: '#cd-modal',
        ajax: false,
        url: '',
        maskClosable: true,
      }).on('ok', ($modal, modal) => {
        console.log('');
        modal.trigger('close');
      }).on('cancel', ($modal, modal) => {
        console.log('');
      })
    }
  }
}
</script>


