<template>
  <div class="tag-page">
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.tools_text_color|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tools_text_color }}
            </code></pre>
          </div>
          <div slot="style">
            <span class="cd-dark-major"></span><br><br>
            <span class="cd-dark-minor"></span><br><br>
            <span class="cd-dark-assist"></span><br><br>
            <div style="background-color: #313131; padding: 5px; display:inline-block">
              <span class="cd-light-major"></span><br><br>
              <span class="cd-light-minor"></span><br><br>
              <span class="cd-light-assist"></span>
            </div>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.tools_spacing|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tools_spacing }}
            </code></pre>
          </div>
          <div slot="style" >
            <pre v-highlightjs><code class="html">
              {{ code.tools_spacing_example }}
            </code></pre>
          </div>
          <div class="" slot="dec">
            <code class="code">*</code>
            <code class="code">0</code>
            <code class="code">8</code>
            <code class="code">16</code>
            <code class="code">24</code>
            <code class="code">32</code>
            <code class="code">40</code>
            <code class="code">0px</code>
            <code class="code">8px</code>
            <code class="code">16px</code>
            <code class="code">24px</code>
            <code class="code">32px</code>
            <code class="code">40px</code>
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.tools_text_size|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tools_text_size }}
            </code></pre>
          </div>
          <div slot="style" >
            <span class="cd-text-xxxlg"></span><br><br>
            <span class="cd-text-xxlg"></span><br><br>
            <span class="cd-text-xlg"></span><br><br>
            <span class="cd-text-lg"></span><br><br>
            <span class="cd-text-md"></span><br><br>
            <span class="cd-text-sm"></span>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.tools_link|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tools_link }}
            </code></pre>
          </div>
          <div slot="style">
            <a href="javascript:;" class="cd-link-major"></a><br><br>
            <a href="javascript:;" class="cd-link-minor"></a><br><br>
            <a href="javascript:;" class="cd-link-assist"></a>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
  </div>
</template>

<script>
import * as code from './tools';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'close',
    dec: 'tag',
    callback: '--',
    args: 'args1: $tagDomjquery'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  created() {
    cd.tag({
      el: '#cd-tag',
    }).on('close', ($close, $tag) => {
      console.log('tag', $close, $tag);
    });
  },
  methods: {
  }
}
</script>

<style lang="less">
.tag-page .cd-tag {
  margin-bottom: 16px;
  margin-right: 16px;
}
</style>



