<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des"></div>
    <x-panel :code="code.confirm_api|trim">
      <div slot="title">
        
      </div>
      <div slot="code">
        <pre v-highlightjs><code class="js">
          {{ code.confirm_api }}
        </code></pre>
      </div>
      <div class="cd-mb16" slot="style">
        <button class="cd-btn cd-btn-ghost-default" id="cofirm-btn" @click="onConfirm"></button>
      </div>
      <div class="" slot="dec">
      </div>
    </x-panel>

    <x-api-table 
      :code="code.confirm_api" 
      :attrData="attrData"
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './confirm';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'title',
    dec: '',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'content',
    dec: '',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'okText',
    dec: '',
    type: 'String',
    value: 'Confirm',
    optional: '--'
  },
  {
    name: 'cancelText',
    dec: '',
    type: 'String',
    value: 'Cancel',
    optional: '--'
  },
  {
    name: 'className',
    dec: 'Class',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'ok',
    dec: '',
    callback: '--',
    args: ''
  },
  {
    name: 'cancel',
    dec: '',
    callback: '--',
    args: ''
  },
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  methods: {
    onConfirm() {
      cd.confirm({
        title: '',
        content: '',
        okText: '',
        cancelText: '',
        className: '',
      }).on('ok', () => {
        console.log('');
      }).on('cancel', () => {
        console.log('');
      })
    }
  },
  mounted() {
  }
}
</script>



