<template>
  <div class="tag-page">
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.tag_type|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tag_type }}
            </code></pre>
          </div>
          <div slot="style" v-html="code.tag_type"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.tag_color|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tag_color }}
            </code></pre>
          </div>
          <div slot="style">
            <span class="cd-tag cd-tag-default"></span><span class="cd-tag cd-tag-green"></span><span class="cd-tag cd-tag-red"></span><span class="cd-tag cd-tag-orange"></span>
            <br>
            <span class="cd-tag cd-tag-blue"></span><span class="cd-tag cd-tag-purple"></span><span class="cd-tag cd-tag-pink"></span><span class="cd-tag cd-tag-yellow"></span>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.tag_api" 
      :attrData="attrData" 
      :hookData="hookData"
    ></x-api-table>
  </div>
</template>

<script>
import * as code from './tag';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'close',
    dec: 'tag',
    callback: '--',
    args: 'args1: $tagDomjquery'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  created() {
    cd.tag({
      el: '#cd-tag',
    }).on('close', ($close, $tag) => {
      console.log('tag', $close, $tag);
    });
  },
  methods: {
  }
}
</script>

<style lang="less">
.tag-page .cd-tag {
  margin-bottom: 16px;
  margin-right: 16px;
}
</style>



