
export const select_base = `
<div class="cd-select" id="select-single">
  <input type="hidden" value="option1" />
  <div class="select-value">1</div>
  <ul class="select-options">
    <li class="checked" data-value="option1">1</li>
    <li data-value="option2">2</li>
    <li data-value="option3">3</li>
  </ul>
</div>
`;

export const select_multi = `
<div class="cd-select cd-select-multi" id="select-multi">
  <input type="hidden" value="option1,option2,option3" />
  <div class="select-value">
  </div>
  <ul class="select-options">
    <li class="checked" data-value="option1">1</li>
    <li class="checked" data-value="option2">2</li>
    <li class="checked" data-value="option3">3</li>
    <li data-value="option4">4</li>
    <li data-value="option5">5</li>
    <li data-value="option6">6</li>
    <li data-value="option7">7</li>
  </ul>
</div>
`;

export const select_api = `
cd.select({
  el: '#select-multi',
  type: 'multi'
}).on('change', (value, text) => {
  console.log('multi', value, text);
});
`;


