<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des">
      
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.tabs_base|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tabs_base }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.tabs_base"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.tabs_ajax|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tabs_ajax }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.tabs_ajax"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.tabs_switch|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.tabs_switch }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.tabs_switch"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.tabs_api" 
      :dataApiData="dataApiData" 
      :attrData="attrData" 
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './tabs';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';
import '@/mock';

const dataApiData = [
  {
    name: 'data-toggle',
    dec: 'JS',
    type: 'String',
    value: 'cd-tabs'
  },
  {
    name: 'data-target',
    dec: 'dom',
    type: 'String',
    value: '--'
  }
];

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'url',
    dec: 'ajax',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'isLoading',
    dec: 'ajaxloading',
    type: 'Boolean',
    value: 'false',
    optional: '--'
  },
  {
    name: 'target',
    dec: 'ajaxDom',
    type: 'String',
    value: '',
    optional: '--'
  }
];

const hookData = [
  {
    name: 'success',
    dec: 'ajax',
    callback: '--',
    args: 'args1: response, ajax'
  },
  {
    name: 'error',
    dec: 'ajax',
    callback: '--',
    args: 'args1: response, ajax'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      dataApiData,
      attrData,
      hookData
    }
  },
  methods: {
  },
  mounted() {
    cd.tabs({
      el: '#cd-tabs a',
      target: '#tabs-panel',
      url: '/demo/tabs/1',
    }).on('success', (res) => {
      let jsonRes = JSON.parse(res);
      $('#tabs-panel').html(jsonRes.message + jsonRes.id);
    }).on('error', (res) => {
      console.log('error', res);
    })
  }
}
</script>

<style lang="less">

</style>



