<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des">
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.upload_avatar|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.upload_avatar }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.upload_avatar"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.upload_file|trim">
          <span slot="title">
            
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.upload_file }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.upload_file"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.upload_api" 
      :attrData="attrData" 
      :hookData="hookData"
    ></x-api-table>
  </div>
</template>

<script>
import * as code from './upload';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'fileSize',
    dec: 'M',
    type: 'String',
    value: '2',
    optional: '--'
  },
  {
    name: 'isLimitFileType',
    dec: '',
    type: 'Boolean',
    value: 'false',
    optional: '--'
  },
  {
    name: 'fileTypes',
    dec: 'isLimitFileTypetrue',
    type: 'Array',
    value: "['image/bmp', 'image/jpeg', 'image/png']",
    optional: '--'
  },
];

const hookData = [
  {
    name: 'success',
    dec: '',
    callback: '--',
    args: `
      args1: eventDom<br>
      args2: filenametype<br>
      args3: srcbase64
    `
  },
  {
    name: 'error',
    dec: '',
    callback: '--',
    args: `
      args1: codeFILE_SIZE_LIMITFLIE_TYPE_LIMIT<br>
    `
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  methods: {
  },
  mounted() {
    cd.upload({
      el: '#cd-upload',
    }).on('success', (event, file, src) => {
      let $this = $(event.currentTarget);
      let $target = $($this.data('target'));

      $target.attr('src', src);
    }).on('error', (code) => {
      let message = '';
      if (code === 'FILE_SIZE_LIMIT') {
        message = '';
      } else if (code === 'FLIE_TYPE_LIMIT') {
        message = '';
      }
      cd.message({
        type: 'danger',
        message: message
      })
    });

    cd.upload({
      el: '#upload-file',
    }).on('success', (event, file, src) => {
      let $this = $(event.currentTarget);
      let $target = $($this.data('target'));

      $this.siblings().text(file.name);
    }).on('error', (code) => {
      let message = '';
      if (code === 'FILE_SIZE_LIMIT') {
        message = '';
      } else if (code === 'FLIE_TYPE_LIMIT') {
        message = '';
      }
      cd.message({
        type: 'danger',
        message: message
      })
    });
  }
}
</script>

<style lang="less">

</style>



