<template>
  <div class="button-page">
    <div class="site-content-title"></div>
    <div class="site-content-des">
       Codeages Design 
    </div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.button_base|trim">
          <span slot="title">
            Button
          </span>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.button_base }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.button_base"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.button_link|trim">
          <div slot="title">
            Link Button
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.button_link }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.button_link">
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.button_disabled|trim">
          <div slot="title">
            Disabled Button
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.button_disabled }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.button_disabled"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.button_ghost|trim">
          <div slot="title">
            Ghost Button
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.button_ghost }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-ghost-primary"></button>
            <button class="cd-btn cd-btn-ghost-default"></button>
            <span style="background: #313131; padding: 8px; display:inline-block; border-radius: 4px;">
              <button class="cd-btn cd-btn-ghost-light" style="margin-right:0"></button>
            </span>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
        <x-panel :code="code.button_size|trim">
          <div slot="title">
            Button Size
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.button_size }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.button_size"></div>
          <div class="" slot="dec">
            Codeages Design 
          </div>
        </x-panel>
        <x-panel :code="code.button_api|trim">
          <div slot="title">
            Button Loading
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="js">
              {{ code.button_api }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style">
            <button class="cd-btn cd-btn-primary" @click="loading"></button>
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>

    <x-api-table :attrData="attrData" :eventData="eventData" :code="code.button_api">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './button';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
  {
    name: 'loadingText',
    dec: 'loading',
    type: 'String',
    value: 'loading...',
    optional: '--',
    data: 'loading-text'
  }
];

const eventData = [
  {
    name: 'loading',
    dec: 'loading',
    callback: '--',
    args: 'args1: $elDomjquery'
  },
  {
    name: 'reset',
    dec: 'loading',
    callback: '--',
    args: 'args1: $elDomjquery'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      eventData,
    }
  },
  methods: {
    loading(event) {
      const btn = cd.btn({
        el: event.currentTarget,
        loadingText: '...'
      });

      btn.trigger('loading', () => {
        console.log('loading...');
      });

      setTimeout(() => {
        btn.trigger('reset', () => {
          console.log('reset...');
        });
      }, 1000);
    }
  }
}
</script>

<style lang="less">
.button-page .cd-btn {
  margin-right: 16px;
}
</style>



