export const tabs_base = `
<ul class="cd-tabs">
  <li class="active"><a href="javascript:;"></a></li>
  <li><a href="javascript:;"><span class="number">2</span></a></li>
  <li><a href="javascript:;"></a></li>
</ul>
`;

export const tabs_switch = `
<ul class="cd-tabs">
  <li class="active"><a href="javascript:;" data-toggle="cd-tabs" data-target="#tab1"></a></li>
  <li><a href="javascript:;" data-toggle="cd-tabs" data-target="#tab2"></a></li>
  <li><a href="javascript:;" data-toggle="cd-tabs" data-target="#tab3"></a></li>
</ul>

<div class="cd-tabs-content">
  <div class="cd-tabs-panel active" id="tab1">
  
  </div>
  <div class="cd-tabs-panel" id="tab2">
  
  </div>
  <div class="cd-tabs-panel" id="tab3">
  
  </div>
</div>
`;

export const tabs_ajax = `
<ul class="cd-tabs" id="cd-tabs">
  <li class="active"><a href="javascript:;" data-url="/demo/tabs/1"></a></li>
  <li><a href="javascript:;" data-url="/demo/tabs/2"></a></li>
  <li><a href="javascript:;" data-url="/demo/tabs/3"></a></li>
</ul>
<div id="tabs-panel"></div>
`;

export const tabs_api = `
cd.tabs({
  el: '#cd-tabs a',
  target: '#tabs-panel',
  url: '/demo/tabs/1',
}).on('success', (res) => {
  console.log('success', res);
}).on('error', (res) => {
  console.log('error', res);
})
`;


