<template>
  <div>
    <div class="site-content-title"></div>
    <div class="site-content-des"></div>
    <div class="cd-row">
      <div class="cd-xs-12">
        <x-panel :code="code.alert_color|trim">
          <div slot="title">
            
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.alert_color }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.alert_color"></div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
      <div class="cd-xs-12">
        <x-panel :code="code.alert_close|trim">
          <div slot="title">
            
          </div>
          <div slot="code">
            <pre v-highlightjs><code class="html">
              {{ code.alert_close }}
            </code></pre>
          </div>
          <div class="cd-mb16" slot="style" v-html="code.alert_close">
          </div>
          <div class="" slot="dec">
            
          </div>
        </x-panel>
      </div>
    </div>
    <x-api-table 
      :code="code.alert_api" 
      :attrData="attrData" 
      :hookData="hookData">
    </x-api-table>
  </div>
</template>

<script>
import * as code from './alert';
import XPanel from '@/components/Panel';
import XApiTable from '@/components/ApiTable';

const attrData = [
  {
    name: 'el',
    dec: 'Dom',
    type: 'String',
    value: '',
    optional: '--'
  },
];

const hookData = [
  {
    name: 'close',
    dec: '',
    callback: '--',
    args: 'args1: $alertDomjquery'
  }
];

export default {
  components: {
    XPanel,
    XApiTable
  },
  data() {
    return {
      code,
      attrData,
      hookData
    }
  },
  mounted() {
    cd.alert({
      el: '.js-cd-alert',
    }).on('close', () => {
      console.log('');
    })
  }
}
</script>



