export const button_base = `
<button class="cd-btn cd-btn-primary"></button>
<button class="cd-btn cd-btn-default"></button>
`;

export const button_link = `
<button class="cd-btn cd-btn-link-primary"></button>
<button class="cd-btn cd-btn-link-default"></button>
<button class="cd-btn cd-btn-link-danger"></button>
`;

export const button_ghost = `
<button class="cd-btn cd-btn-ghost-primary"></button>
<button class="cd-btn cd-btn-ghost-default"></button>
<button class="cd-btn cd-btn-ghost-light"></button>
`;

export const button_disabled = `
<button disabled class="cd-btn cd-btn-default"></button><button disabled class="cd-btn cd-btn-ghost-default"></button><button disabled class="cd-btn cd-btn-link-default"></button>
`;

export const button_size = `
<button class="cd-btn cd-btn-primary cd-btn-lg"></button>
<button class="cd-btn cd-btn-primary"></button>
<button class="cd-btn cd-btn-primary cd-btn-sm"></button>
`;

export const button_api = `
const btn = cd.btn({
  el: '#cd-btn',
  loadingText: '...'
});

btn.trigger('loading', () => {
  console.log('loading...');
});

setTimeout(() => {
  btn.trigger('reset', () => {
    console.log('reset...');
  });
}, 1000);
`;


